import Product from "./product"
import Pagination from "../pagination"
import Items from "../items"
import ProductsSorting from "./sorting"

function Products(props) {
  const { Suspense } = wp.element

  return (
    <Suspense fallback={false}>
      <Items
        {...props}
        queryType={props.queryType ? props.queryType : "products"}
        componentType="products"
      >
        {props.settings.withSorting ? (
          <ProductsSorting isFilteringCollections={props.settings.collection} />
        ) : null}
        <Pagination {...props}>
          <Product />
        </Pagination>
      </Items>
    </Suspense>
  )
}

export default wp.element.memo(Products)
