import { ItemsProvider } from "./_state/provider"
import { usePortal } from "Hooks"
import Item from "./item"

function ItemsSkeleton(props) {
  return <div className="shopwp-skeleton">{props.loader}</div>
}
function Items(props) {
  return (
    <ItemsProvider {...props}>
      {usePortal(<Item>{props.children}</Item>, props.element)}
      {props.loader ? (
        props.element ? (
          usePortal(<ItemsSkeleton {...props} />, props.element)
        ) : (
          <ItemsSkeleton {...props} />
        )
      ) : null}
    </ItemsProvider>
  )
}

export default Items
