import { post } from "../request"

function createCart(data, shopState) {
  return post("cart/create", data, shopState)
}

function updateCartAttributes(data, shopState) {
  return post("cart/attributes/update", data, shopState)
}

function updateBuyerIdentity(data, shopState) {
  return post("cart/buyer/update", data, shopState)
}

function getCart(id, shopState) {
  return post(
    "cart/get",
    {
      id: id,
    },
    shopState
  )
}

function addLineItems(data, shopState) {
  return post("cart/lineitems/add", data, shopState)
}

function removeLineItems(data, shopState) {
  return post("cart/lineitems/remove", data, shopState)
}

function updateLineItems(data, shopState) {
  return post("cart/lineitems/update", data, shopState)
}

function applyDiscount(data, shopState) {
  return post("cart/discount", data, shopState)
}

function updateNote(data, shopState) {
  return post("cart/note/update", data, shopState)
}

export {
  createCart,
  applyDiscount,
  getCart,
  addLineItems,
  removeLineItems,
  updateLineItems,
  updateNote,
  updateCartAttributes,
  updateBuyerIdentity,
}
