/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import Selects from "../../storefront/selects"
import { findDefaultSelectVal, updateQueryParamsWithSort } from "Utils"
import Loader from "../../loader"
import { useItemsState, useItemsDispatch } from "../../items/_state/hooks"

function ProductsSorting({
  isFilteringCollections,
  dropzone,
  showLoader = true,
}) {
  const itemsDispatch = useItemsDispatch()
  const itemsState = useItemsState()

  const sortingWrapperCSS = css`
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-bottom: 20px;
    align-items: baseline;

    #swp-collections-sorting *:hover {
      cursor: pointer;
    }
  `

  const collectionOptions = shopwp.storefront.sortingOptionsCollections
  const productOptions = shopwp.storefront.sortingOptionsProducts

  function customOnChange(data) {
    const params = updateQueryParamsWithSort(data.value)

    itemsDispatch({
      type: "SET_QUERY_PARAMS",
      payload: {
        ...itemsState.queryParams,
        ...params,
      },
    })

    itemsDispatch({
      type: "SET_IS_FETCHING_NEW",
      payload: true,
    })
  }

  return !itemsState.isBootstrapping ||
    itemsState.componentType === "storefront" ? (
    <div css={sortingWrapperCSS}>
      {itemsState.isLoading && showLoader && <Loader color="#000" />}
      <Selects
        dropzone={dropzone ? dropzone : false}
        labelText={wp.i18n.__("Sort by:", "shopwp")}
        selectId="swp-products-sorting"
        options={isFilteringCollections ? collectionOptions : productOptions}
        customOnChange={customOnChange}
        defaultValue={findDefaultSelectVal(
          isFilteringCollections ? collectionOptions : productOptions,
          itemsState.settings.sortBy,
          itemsState.settings.reverse
        )}
        isLoading={itemsState.isLoading}
      />
    </div>
  ) : null
}

export default ProductsSorting
