import { post } from "../request"

function getAllTags(shopState) {
  return post("products/tags", {}, shopState)
}

function getAllVendors(shopState) {
  return post("products/vendors", {}, shopState)
}

function getAllTypes(shopState) {
  return post("products/types", {}, shopState)
}

function getProducts(data, shopState) {
  return post("query/products", data, shopState)
}

function getProductById(data, shopState) {
  return post("query/product/id", data, shopState)
}

function getProductsByCollections(data, shopState) {
  return post("query/products/collections", data, shopState)
}

export {
  getAllTags,
  getAllVendors,
  getAllTypes,
  getProducts,
  getProductById,
  getProductsByCollections,
}
